html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Gidole", "Arial", sans-serif;
  color: #000000;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  overflow-x: hidden;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  /* stylelint-disable */
  font-weight: 400 !important;
  /* stylelint-enable */
  /* stylelint-disable */
  text-transform: lowercase !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  width: 100%;
  background-color: transparent;
  transition: transform 0.6s cubic-bezier(0.55, 0, 0.1, 1);
}
.header .container {
  padding: 40px;
  margin: 0;
  position: relative;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: transform 0.3s ease;
}
.header__logo {
  font-size: 40px;
  line-height: 40px;
  color: #7200cf;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .header__logo:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.header__logo.focus-visible:focus {
  opacity: 0.8;
}
.header__toggle {
  position: relative;
  width: 36px;
  height: 40px;
  padding: 0;
  border: 0;
  background: 0 0;
  cursor: pointer;
}
@media (hover: hover) {
  .header__toggle:hover:not(.focus-visible)::before {
    transform: translate3d(-5px, 0, 0);
  }
  .header__toggle:hover:not(.focus-visible)::after {
    transform: translate3d(5px, 0, 0);
  }
}
.header__toggle.focus-visible:focus::before {
  transform: translate3d(-5px, 0, 0);
}
.header__toggle.focus-visible:focus::after {
  transform: translate3d(5px, 0, 0);
}
.header__toggle::after, .header__toggle::before {
  content: "";
  position: absolute;
  left: 0;
  width: 36px;
  height: 3px;
  transition: transform 0.3s ease, background-color 0.3s ease;
  background-color: #7200cf;
}
.header__toggle::before {
  bottom: 13px;
}
.header__toggle::after {
  top: 13px;
}
.header .main-nav {
  transform: translateY(-100%);
}
@media (hover: hover) {
  .header.is-active .header__toggle:hover:not(.focus-visible)::before {
    transform: translate3d(0, -6px, 0) rotate(35deg);
  }
  .header.is-active .header__toggle:hover:not(.focus-visible)::after {
    transform: translate3d(0, 6px, 0) rotate(-35deg);
  }
}
.header.is-active .header__toggle.focus-visible:focus::before {
  transform: translate3d(0, -6px, 0) rotate(35deg);
}
.header.is-active .header__toggle.focus-visible:focus::after {
  transform: translate3d(0, 6px, 0) rotate(-35deg);
}
.header.is-active .header__toggle::after {
  transform: translate3d(0, 6px, 0) rotate(-40deg);
}
.header.is-active .header__toggle::before {
  transform: translate3d(0, -6px, 0) rotate(40deg);
}
.header.is-active .main-nav {
  transform: translateY(0);
}

.main-nav {
  position: fixed;
  top: 0;
  padding-top: 100px;
  width: 100%;
  height: 100vh;
  transition: transform 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  background: #574747;
}
.main-nav__list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-nav__item {
  margin-bottom: 45px;
  overflow: hidden;
}
.main-nav__link {
  display: block;
  font-size: 40px;
  line-height: 40px;
  color: #ffffff;
  transition: transform 0.3s ease;
  transform: translate3d(0, 110%, 0);
}
.main-nav__link.is-active {
  color: #7200cf;
  pointer-events: none;
}
@media (hover: hover) {
  .main-nav__link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.main-nav__link.focus-visible:focus {
  opacity: 0.8;
}
.main-nav.is-active .main-nav__item:nth-child(1) .main-nav__link {
  transition-delay: 0.45s;
  transform: translate3d(0, 0, 0);
}
.main-nav.is-active .main-nav__item:nth-child(2) .main-nav__link {
  transition-delay: 0.55s;
  transform: translate3d(0, 0, 0);
}
.main-nav.is-active .main-nav__item:nth-child(3) .main-nav__link {
  transition-delay: 0.55s;
  transform: translate3d(0, 0, 0);
}
.main-nav.is-active .main-nav__item:nth-child(4) .main-nav__link {
  transition-delay: 0.55s;
  transform: translate3d(0, 0, 0);
}
.main-nav.is-active .main-nav__item:nth-child(5) .main-nav__link {
  transition-delay: 0.55s;
  transform: translate3d(0, 0, 0);
}

.footer {
  padding: 40px 0;
  background-color: #2c39f2;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

