  overflow: hidden;
}
.curtains__text {
  display: block;
  transform: translateY(500%);
}

.scroll-slider {
  position: relative;
}
.scroll-slider__sticky {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  position: sticky;
  top: 0;
}
.scroll-slider__inner {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100vh;
}
.scroll-slider__inner:first-child .scroll-slider__wrap {
  clip-path: polygon(0 100%, 100% 100%, 100% 0, 0 0);
}
.scroll-slider__wrap {
  width: 100%;
  height: 100%;
  clip-path: polygon(0 100%, 100% 100%, 100% 100%, 0 100%);
  background-size: cover;
  background-position: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  max-width: 100%;
}
.scroll-slider__wrap h2 {
  margin: 0 0 20px;
  font-size: 25px;
}
.scroll-slider__wrap p {
  margin: 0 0 20px;
  font-size: 20px;
  text-align: center;
}
.scroll-slider__wrap::before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #fff;
  opacity: 0.6;
  filter: blur(70px);
}
.scroll-slider__wrap.one {
  background-image: url("../img/content/bg-1.jpg");
}
.scroll-slider__wrap.two {
  background-image: url("../img/content/bg-2.jpg");
}
.scroll-slider__wrap.three {
  background-image: url("../img/content/bg-3.jpg");
}
.scroll-slider__wrap.four {
  background-image: url("../img/content/bg-4.jpg");
}

